$blackColor: #333;
$whiteColor: #fff;
$greyColor: #e8e8e8;
$primaryColor: #08aef1;
$secondaryColor: #d9ab4d;
$successColor: #87bf0a;
$errorColor: #e82c0c;
$freeSpace: 15px;
$headerHeight: 49px;
$footerHeight: 49px;
$borderRadius: 2px;


@import "reset";
@import "grid";
@import "header";
@import "main";
@import "footer";
@import "typography";
@import "components/post";
@import "forms";
